      @refresherrefresh="handleRefresh"
      @refresherrestore="handleRefreshRestore"
      @scrolltolower="handleLoadMore"
      :lower-threshold="100"
    >
      <view class="list-wrapper">
        <!-- ÂàùÂßãÂä†ËΩΩÁä∂ÊÄÅ -->
        <view v-if="MesStore.loading && MesStore.MessageList.length === 0" class="loading-container">
          <view class="loading-spinner"></view>
          <text class="loading-text">Ê≠£Âú®Âä†ËΩΩÊ∂àÊÅØ...</text>
        </view>

        <!-- Ê∂àÊÅØÂàóË°® -->
        <view v-else class="messages-container">
          <!-- Ê∂àÊÅØÈ°π -->
          <view 
            v-for="(message, index) in MesStore.MessageList" 
            :key="message.id"
            class="message-item"
            :class="{ 'expanded': message.expanded }"
          >
            <!-- Ê∂àÊÅØÂç°Áâá -->
            <view class="message-card" @tap="handleMessageClick(message, index)">
              <!-- Ë£ÖÈ•∞Á∫øÊù° -->
              <view class="decoration-line"></view>
              
              <!-- Ê∂àÊÅØÂ§¥ÈÉ® -->
              <view class="message-header">
                <view class="message-icon">
                  <text class="icon-text">üì¢</text>
                </view>
                <view class="header-content">
                  <view class="title-line">
                    <text class="message-title">{{ message.title || 'Á≥ªÁªüÈÄöÁü•' }}</text>
                    <!-- Êó∂Èó¥ÁßªÂà∞Âè≥‰∏äËßí -->
                    <text class="message-time">{{ formatTime(message.send_time || message.created_at) }}</text>
                  </view>
                  <!-- ÂèëÈÄÅËÄÖ‰ø°ÊÅØÔºàÂ¶ÇÊûúÊúâÔºâ -->
                  <view v-if="message.sender_name" class="sender-info">
                    <text class="sender-name">{{ message.sender_name }}</text>
                  </view>
                </view>
              </view>

              <!-- Ê∂àÊÅØÂÜÖÂÆπÂå∫Âüü -->
              <view class="message-body">
                <!-- Áü≠ÂÜÖÂÆπÁõ¥Êé•ÊòæÁ§∫ -->
                <view v-if="getContentType(message.content) === 'short'" class="content-wrapper">
                  <text class="content-text">{{ message.content }}</text>
                </view>

                <!-- ‰∏≠Á≠âÈïøÂ∫¶ÂÜÖÂÆπÂèØÊäòÂè† -->
                <view v-else-if="getContentType(message.content) === 'medium'" class="content-wrapper">
                  <text 
                    class="content-text" 
                    :class="{ 'content-collapsed': !message.expanded }"
                  >
                    {{ message.content }}
                  </text>
                  
                  <!-- ÊäòÂè†/Â±ïÂºÄÊéßÂà∂ -->
                  <view class="toggle-section">
                    <view class="fade-mask" v-if="!message.expanded"></view>
                    <view class="toggle-btn" @tap.stop="toggleContent(index)">
                      <text class="toggle-text">{{ message.expanded ? 'Êî∂Ëµ∑' : 'Â±ïÂºÄ' }}</text>
                      <view class="toggle-icon" :class="{ 'rotated': message.expanded }">
                        <text class="icon-arrow">‚ñº</text>
                      </view>
                    </view>
                  </view>
                </view>

                <!-- Ë∂ÖÈïøÂÜÖÂÆπÊòæÁ§∫È¢ÑËßà -->
                <view v-else class="content-wrapper">
                  <text class="content-preview">{{ getPreviewText(message.content) }}</text>
                  
                  <view class="action-section">
                    <view class="detail-btn" @tap.stop="viewFullContent(message)">
                      <view class="btn-content">
                        <text class="btn-text">Êü•ÁúãÂÆåÊï¥ÂÜÖÂÆπ</text>
                        <view class="btn-arrow">
                          <text class="arrow-icon">‚Üí</text>
                        </view>
                      </view>
                    </view>
                  </view>
                </view>

                <!-- Ê∂àÊÅØÁä∂ÊÄÅÊ†è -->
                <view class="message-status" v-if="message.priority || message.category">
                  <view v-if="message.priority" class="priority-tag" :class="`priority-${message.priority}`">
                    <text class="priority-text">{{ getPriorityText(message.priority) }}</text>
                  </view>
                  <view v-if="message.category" class="category-tag">
                    <text class="category-text">{{ message.category }}</text>
                  </view>
                </view>
              </view>
            </view>
          </view>

          <!-- Âä†ËΩΩÊõ¥Â§öÁä∂ÊÄÅ -->
          <view v-if="MesStore.hasMoreData && MesStore.MessageList.length > 0" class="load-more-section">
            <view v-if="MesStore.loadingMore" class="loading-more">
              <view class="loading-spinner small"></view>
              <text class="loading-text">Âä†ËΩΩÊõ¥Â§öÊ∂àÊÅØ...</text>
            </view>
            <view v-else class="load-more-tip">
              <text class="tip-text">‰∏äÊãâÂä†ËΩΩÊõ¥Â§ö</text>
            </view>
          </view>

          <!-- Êó†Êõ¥Â§öÊï∞ÊçÆ -->
          <view v-else-if="MesStore.MessageList.length > 0 && !MesStore.hasMoreData" class="no-more-section">
            <view class="no-more-line"></view>
            <text class="no-more-text">Â∑≤ÊòæÁ§∫ÂÖ®ÈÉ®Ê∂àÊÅØ</text>
            <view class="no-more-line"></view>
          </view>
        </view>

        <!-- Á©∫Áä∂ÊÄÅ -->
        <view v-if="!MesStore.loading && MesStore.MessageList.length === 0" class="empty-state">
          <view class="empty-illustration">
            <view class="empty-circle">
              <text class="empty-icon">üì≠</text>
            </view>
          </view>
          <text class="empty-title">ÊöÇÊó†Á≥ªÁªüÊ∂àÊÅØ</text>
          <text class="empty-desc">Á≥ªÁªüÈáçË¶ÅÈÄöÁü•‰ºöÂú®ËøôÈáåÊòæÁ§∫</text>
          <view class="retry-btn" v-if="MesStore.error" @tap="handleRetry">
            <text class="retry-text">ÈáçÊñ∞Âä†ËΩΩ</text>
          </view>
        </view>

        <!-- ÈîôËØØÊèêÁ§∫ -->
        <view v-if="MesStore.error && MesStore.MessageList.length > 0" class="error-banner">
          <view class="error-content">
            <text class="error-icon">‚ö†Ô∏è</text>
            <text class="error-text">{{ MesStore.error }}</text>
            <view class="retry-small-btn" @tap="handleRetry">
              <text class="retry-small-text">ÈáçËØï</text>
            </view>
          </view>
        </view>
      </view>
    </scroll-view>
  </view>
</template>

<script setup>
import { ref, onMounted, computed, nextTick } from 'vue'
import { onLoad, onShow, onHide } from '@dcloudio/uni-app'
import { useMesstore } from '@/store/mes.js'

// ÂìçÂ∫îÂºèÊï∞ÊçÆ
const statusBarHeight = ref(0)
const pageSize = ref(10)
const loadThrottle = ref(false) // Âä†ËΩΩËäÇÊµÅ

// ÂàùÂßãÂåñpinia
const MesStore = useMesstore()

// ÂÜÖÂÆπÈïøÂ∫¶ÈÖçÁΩÆ
const CONTENT_CONFIG = {
  SHORT_LIMIT: 80,     // Áü≠ÂÜÖÂÆπÈôêÂà∂ÔºàÂ≠óÁ¨¶Êï∞Ôºâ
  MEDIUM_LIMIT: 300,   // ‰∏≠Á≠âÂÜÖÂÆπÈôêÂà∂
  PREVIEW_LENGTH: 120, // È¢ÑËßàÂÜÖÂÆπÈïøÂ∫¶
  COLLAPSE_HEIGHT: 3   // ÊäòÂè†Êó∂ÊòæÁ§∫Ë°åÊï∞
}

// ‰ºòÂÖàÁ∫ßÊñáÊú¨Êò†Â∞Ñ
const PRIORITY_MAP = {
  'high': 'ÈáçË¶Å',
  'medium': 'ÊôÆÈÄö',
  'low': '‰∏ÄËà¨',
  'urgent': 'Á¥ßÊÄ•'
}

// ÁîüÂëΩÂë®Êúü
onMounted(async () => {
  const sysInfo = uni.getSystemInfoSync()
  statusBarHeight.value = sysInfo.statusBarHeight || 0
})

onLoad(async (options) => {
  await loadInitialMessages()
})

onShow(() => {
  // È°µÈù¢ÊòæÁ§∫Êó∂ÂèØ‰ª•ÂÅö‰∏Ä‰∫õÊìç‰ΩúÔºåÊØîÂ¶ÇÂà∑Êñ∞Êï∞ÊçÆÁä∂ÊÄÅÊ£ÄÊü•
  console.log('Á≥ªÁªüÊ∂àÊÅØÈ°µÈù¢ÊòæÁ§∫')
})

onHide(() => {
  // È°µÈù¢ÈöêËóèÊó∂Ê∏ÖÈô§ÈîôËØØÁä∂ÊÄÅ
  if (MesStore.error) {
    MesStore.error = null
  }
})

// ÂàùÂßãÂä†ËΩΩÊ∂àÊÅØ
const loadInitialMessages = async () => {
  try {
    await MesStore.getMessageList({
      message_type: 'SYSTEM',
      page: 1,
      page_size: pageSize.value
    })
  } catch (error) {
    console.error('ÂàùÂßãÂä†ËΩΩÊ∂àÊÅØÂ§±Ë¥•:', error)
    uni.showToast({
      title: 'Âä†ËΩΩÂ§±Ë¥•ÔºåËØ∑ÈáçËØï',
      icon: 'none',
      duration: 2000
    })
  }
}

// ‰ºòÂåñÁöÑËß¶Â∫ïÂä†ËΩΩÊõ¥Â§ö
const handleLoadMore = async () => {
  // Èò≤ÊäñÂ§ÑÁêÜ
  if (loadThrottle.value || MesStore.loadingMore || !MesStore.hasMoreData) {
    console.log('Âä†ËΩΩÊù°‰ª∂‰∏çÊª°Ë∂≥:', {
      throttle: loadThrottle.value,
      loading: MesStore.loadingMore,
      hasMore: MesStore.hasMoreData
    })
    return
  }

  loadThrottle.value = true
  
  try {
    const result = await MesStore.loadMoreMessages({
      message_type: 'SYSTEM',
      page_size: pageSize.value
    })
    
    if (result.success) {
      console.log(`Âä†ËΩΩÊõ¥Â§öÊàêÂäü: Êñ∞Â¢û${result.data.length}Êù°Ê∂àÊÅØ`)
      
      // Â¶ÇÊûúËøîÂõûÊï∞ÊçÆÂ∞ë‰∫éËØ∑Ê±ÇÊï∞ÈáèÔºåËØ¥ÊòéÂ∑≤ÁªèÂà∞Â∫ï‰∫Ü
      if (result.data.length < pageSize.value) {
        console.log('Â∑≤Âä†ËΩΩÊâÄÊúâÊï∞ÊçÆ')
      }
    }
  } catch (error) {
    console.error('Âä†ËΩΩÊõ¥Â§öÂ§±Ë¥•:', error)
    uni.showToast({
      title: 'Âä†ËΩΩÂ§±Ë¥•ÔºåËØ∑ÈáçËØï',
      icon: 'none',
      duration: 2000
    })
  } finally {
    // Âª∂ËøüÈáçÁΩÆËäÇÊµÅÁä∂ÊÄÅÔºåÈÅøÂÖçÈ¢ëÁπÅËß¶Âèë
    setTimeout(() => {
      loadThrottle.value = false
    }, 1000)
  }
}

// ‰ºòÂåñÁöÑ‰∏ãÊãâÂà∑Êñ∞
const handleRefresh = async () => {
  try {
    const result = await MesStore.refreshMessageList({
      message_type: 'SYSTEM',
      page_size: pageSize.value
    })
    
    if (result.success) {
      console.log('Âà∑Êñ∞ÊàêÂäü:', result.data.length, 'Êù°Ê∂àÊÅØ')
      uni.showToast({
        title: 'Âà∑Êñ∞ÊàêÂäü',
        icon: 'success',
        duration: 1500
      })
    }
  } catch (error) {
    console.error('Âà∑Êñ∞Â§±Ë¥•:', error)
    uni.showToast({
      title: 'Âà∑Êñ∞Â§±Ë¥•',
      icon: 'error',
      duration: 2000
    })
  }
}

const handleRefreshRestore = () => {
  console.log('Âà∑Êñ∞ÂÆåÊàê')
}

// ÈáçËØïÂä†ËΩΩ
const handleRetry = async () => {
  MesStore.error = null
  await loadInitialMessages()
}

// Ëé∑ÂèñÂÜÖÂÆπÁ±ªÂûã
const getContentType = (content) => {
  if (!content) return 'short'
  
  const length = content.length
  if (length <= CONTENT_CONFIG.SHORT_LIMIT) {
    return 'short'
  } else if (length <= CONTENT_CONFIG.MEDIUM_LIMIT) {
    return 'medium'
  } else {
    return 'long'
  }
}

// Ëé∑ÂèñÈ¢ÑËßàÊñáÊú¨
const getPreviewText = (content) => {
  if (!content) return ''
  return content.length > CONTENT_CONFIG.PREVIEW_LENGTH 
    ? content.substring(0, CONTENT_CONFIG.PREVIEW_LENGTH) + '...'
    : content
}

// Ëé∑Âèñ‰ºòÂÖàÁ∫ßÊñáÊú¨
const getPriorityText = (priority) => {
  return PRIORITY_MAP[priority] || priority
}

// Ê∂àÊÅØÁÇπÂáªÂ§ÑÁêÜ
const handleMessageClick = async (message, index) => {
  // ÂèØ‰ª•Âú®ËøôÈáåÊ∑ªÂä†ÂÖ∂‰ªñÁÇπÂáªÂ§ÑÁêÜÈÄªËæë
  console.log('ÁÇπÂáªÊ∂àÊÅØ:', message.title)
}

// ÂàáÊç¢ÂÜÖÂÆπÂ±ïÂºÄ/ÊäòÂè†
const toggleContent = async (index) => {
  const message = MesStore.MessageList[index]
  if (!message) return

  // ÂàáÊç¢Â±ïÂºÄÁä∂ÊÄÅ
  MesStore.toggleMessageExpanded(message.id)
}

// Êü•ÁúãÂÆåÊï¥ÂÜÖÂÆπ
const viewFullContent = async (message) => {
  // Ë∑≥ËΩ¨Âà∞Ê∂àÊÅØËØ¶ÊÉÖÈ°µ
  uni.navigateTo({
    url: `/pages/detail/SystemMesDetail?id=${message.id}&title=${encodeURIComponent(message.title || 'Á≥ªÁªüÊ∂àÊÅØ')}`
  })
}

// Ê†ºÂºèÂåñÊó∂Èó¥
const formatTime = (timeStr) => {
  if (!timeStr) return ''
  
  try {
    const time = new Date(timeStr)
    const now = new Date()
    const diff = now - time
    
    if (diff < 60000) {
      return 'ÂàöÂàö'
    } else if (diff < 3600000) {
      return `${Math.floor(diff / 60000)}ÂàÜÈíüÂâç`
    } else if (diff < 86400000) {
      return `${Math.floor(diff / 3600000)}Â∞èÊó∂Ââç`
    } else if (diff < 604800000) {
      return `${Math.floor(diff / 86400000)}Â§©Ââç`
    } else {
      const month = String(time.getMonth() + 1).padStart(2, '0')
      const date = String(time.getDate()).padStart(2, '0')
      const hours = String(time.getHours()).padStart(2, '0')
      const minutes = String(time.getMinutes()).padStart(2, '0')
      
      if (time.getFullYear() === now.getFullYear()) {
        return `${month}-${date} ${hours}:${minutes}`
      } else {
        return `${time.getFullYear()}-${month}-${date}`
      }
    }
  } catch (error) {
    return String(timeStr)
  }
}

// ËøîÂõû‰∏ä‰∏ÄÈ°µ
const goBack = () => {
  uni.navigateBack()
}
</script>

